*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
input {
  outline: none;
  border: none;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type="number"],
input[type="number"]:hover,
input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield;
}
/*    */
ul[class],
ol[class] {
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}

/*    */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/*    -  body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background-color: var(--color-bgc-main);
}

/*      ul  il,     class*/
ul[class],
ol[class] {
  list-style: none;
}

/*  a,    ,     */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/*     */
img {
  max-width: 100%;
  display: block;
}

/*        article*/
article > * + * {
  margin-top: 1em;
}

/*       */
input,
button,
textarea,
select {
  font: inherit;
}
